<template>
  <div>
    <div class="row">
      <div class="col-md-8">
        <router-view/>
      </div>
      <div class="col-md-4">
        <h4>最新博客列表</h4>
        <hr>
        <router-link v-for="blog in blogList" :key="blog.id" class="nav-link" :to="'/'+blog.type+'/'+blog.id">{{blog.title}}</router-link>

        <!-- 
          vue地图切换工具动画，vue-cli3入门教程，vue入门教程
          yarn入门教程
          git入门教程，使用Github搭建个人网站
          bootstrap入门教程，
          axios入门教程
          leaflet入门教程，leaflet+D3绘制雷达图，leaflet绘制热力图
        -->
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      blogList: []
    };
  },
  created() {
    const myList = this.GLOBAL;
    this.blogList = myList.filter(l => l.type === "blogs").reverse();
  }
};
</script>

<style scoped>
.nav-link {
  color: #2c3e50;
}
</style>



